import { UserInterface } from './../../shared/models/user.interface';
import { AuthUserInterface } from 'src/app/shared/models/auth-user.interface';
import { TagsService } from './../../shared/service/tags.service';
import { TagInterface } from './../../shared/models/tag.interface';
import { UsersService } from 'src/app/shared/service/users.service';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class UserManagementService {

  constructor(
    private http: HttpClient,
    private usersService: UsersService,
    private tagsService: TagsService,
  ) { }

  resetPassword(userId: number, password: string): Observable<void> {
    return this.usersService.resetPassword(userId, password);
  }

  getAllTags(): Observable<TagInterface[]> {
    return this.tagsService.getAll();
  }
  getTag(tagKey: any) {
    return this.tagsService.getOne(tagKey);
  }

  getHistory(tagKey: any, subId: any) {
    return this.tagsService.getHistory(tagKey, subId);
  }
  
  addTag(tag: TagInterface): Observable<TagInterface> {
    return this.tagsService.add(tag);
  }

  deleteTag(tagKey: any) {
    return this.tagsService.deleteOne(tagKey);
  }

  updateTag(tagKey:any, tag: TagInterface): Observable<TagInterface> {
    return this.tagsService.update(tagKey, tag);
  }

  updateUser(user: UserInterface): Observable<UserInterface> {
    return this.usersService.updateUser(user);
  }

  getUser(userId: string): Observable<UserInterface> {
    return this.usersService.getUser(userId);
  }
  getSubIds(userId: string, tagKey: string) {
    return this.tagsService.getSubIDs(userId, tagKey);
  }
  RequestTag(tagKey: string, subId: string, limit: string ,browserName: string, browserVersion: string, deviceType: string ) {
    return this.tagsService.RequestTag(tagKey, subId, limit,browserName, browserVersion, deviceType);
  }
  // Get all Chrome objects
  GetChromeBrowserVersion() {
    return this.tagsService.GetChrome();
  }
  // Get all Chrome objects
  GetFirefoxBrowserVersion() {
    return this.tagsService.GetFirefox();
  }
}
