import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../../auth/_services/auth.service';
import { UserManagementService } from '../../user-management.service';
import { SnackbarService } from 'src/app/shared/service/snackbar.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Router, ActivatedRoute } from '@angular/router';
import { MatTableDataSource } from '@angular/material/table';
import { StaticsInterface } from 'src/app/shared/models/statics.interface';

@Component({
  selector: 'app-history-detail',
  templateUrl: './history-detail.component.html',
  styleUrls: ['./history-detail.component.scss']
})
export class HistoryDetailComponent implements OnInit {
  getTagKey: any;
  getSubId: any;
  History: any = [];
  tempHistory: any = [];
  staticsColumnDisplay = ['date', 'tagKey', "totalRequests", 'allowedRequests', 'deniedRequests'];
  statics = new MatTableDataSource<StaticsInterface>();
  constructor(private userManagementService: UserManagementService,
    private authService: AuthService,
    private route: ActivatedRoute,
    private sS: SnackbarService,
    private modalService: NgbModal,
    private router: Router

  ) {
    
   }

  ngOnInit(): void {
    this.getSubId = this.route.snapshot.params.subid;
    this.getTagKey = this.route.snapshot.params.tagKey;
    this.userManagementService.getHistory(this.getTagKey, this.getSubId).subscribe((resHistory: any) => {
      this.statics.data = resHistory;
      
      
    });
  }

}
