import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { TagInterface } from '../models/tag.interface';

const API_TAGS_URL = `${environment.apiUrl}/tags`;


@Injectable({
  providedIn: 'root'
})
export class TagsService {

  constructor(
    private http: HttpClient
  ) { }

  getAll(): Observable<TagInterface[]> {
    return this.http.get<TagInterface[]>(API_TAGS_URL);
  }

  getOne(tagKey:any) {
    return this.http.get(API_TAGS_URL + `/${tagKey}`);
  }

  getHistory(tagKey:any, subId:any) {
    return this.http.get(API_TAGS_URL + "/all-history" + `/${tagKey}` + `/${subId}`);
  }

  deleteOne(tagKey:any) {
    return this.http.delete(API_TAGS_URL + `/${tagKey}`);
  }

  add(tag:TagInterface): Observable<TagInterface> {
    return this.http.post<TagInterface>(API_TAGS_URL,tag);
  }
  update(tagKey:any, tag:TagInterface): Observable<TagInterface> {
    return this.http.post<TagInterface>(API_TAGS_URL+ `/update-tag/${tagKey}`,tag);
  }
  GetChrome(){
    return this.http.get(API_TAGS_URL + "/chrome/browser");
  }
  GetFirefox(){
    return this.http.get<any>(API_TAGS_URL + "/firefox/browser");
  }
  getSubIDs(userId: string, tagKey: string) {
    return this.http.get(`${API_TAGS_URL}/get_subids` + `/${userId}` + `/${tagKey}`);
  }
  RequestTag(tagKey: string,subid: string, limit:string, browserName: string, browserVersion: string, deviceType: string) {
    let params = new HttpParams().set('tag', tagKey).set('subid', subid).set('limit', limit).set('browserName', browserName).set('browserVersion', browserVersion).set('deviceType', deviceType);
    return this.http.get(`${API_TAGS_URL}/history/go/`, {params: params});
  }
}
