import { Router, ActivatedRoute } from '@angular/router';
import { TagInterface } from './../../../shared/models/tag.interface';
import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../auth/_services/auth.service';
import { UserManagementService } from '../user-management.service';
import { UserInterface } from 'src/app/shared/models/user.interface';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { UserPasswordResetComponent } from './user-password-reset/user-password-reset.component';
import { SnackbarService } from 'src/app/shared/service/snackbar.service';
import { AddTagComponent } from './add-tag/add-tag.component';
import { EditTagComponent } from './edit-tag/edit-tag.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { MatTableDataSource } from '@angular/material/table';

@Component({
  selector: 'app-edit-users',
  templateUrl: './edit-users.component.html',
  styleUrls: ['./edit-users.component.scss']
})
export class EditUsersComponent implements OnInit {

  userProfileFG: FormGroup;
  tagsColumnDisplay = ['name', 'company', 'advertiser', 'limit', 'browser', 'device', 'browserVersion', 'country', "action"];
  tags = new MatTableDataSource<TagInterface>();
  user: UserInterface;
  userId: any;
  tempTag = [];
  tempTagId = [];

  constructor(
    private userManagementService: UserManagementService,
    private authService: AuthService,
    private route: ActivatedRoute,
    private sS: SnackbarService,
    private modalService: NgbModal,
    private router: Router,
  ) { }

  ngOnInit(): void {

    this.userProfileFG = new FormGroup({
      fullname: new FormControl('', Validators.required),
      email: new FormControl('', [Validators.required, Validators.email])
    })
    this.userId = this.route.snapshot.params.id;
    if (this.route.snapshot.params.id) {
      this.userManagementService.getUser(this.route.snapshot.params.id).subscribe(x => {
        this.user = x;
        this.tags.data = x.tags;
        this.tempTag = x.tags;
        this.tempTagId = x.tagsId;
        this.userProfileFG.patchValue(x);
      });
    } else {
      this.user = this.authService.currentUserSubject.value as any;
    }
    
  }

  onProfileSubmit($event: any): void {
    if (this.userProfileFG.valid) {
      this.user = { ...this.user, ...this.userProfileFG.value };
      this.userManagementService.updateUser(this.user).subscribe(x => {
        this.sS.info('user profile updated');
      });
    }
  }

  openPasswordResetDialog() {

    const modalRef = this.modalService.open(UserPasswordResetComponent, {
      size: 'sm'
    });

    modalRef.componentInstance.data = {
      id: this.user._key
    };

    modalRef.result.then(c => {

    })
  }

  openAddTagDialog() {
    const modalRef = this.modalService.open(AddTagComponent, {
      size: 'lg'
    });
    modalRef.componentInstance.data = {};

    modalRef.result.then(c => {
      var tags = this.user.tags || [];
      var tagsId = this.user.tagsId || [];
      tags.push(c);
      tagsId.push(c._key);

      this.user.tagsId = tagsId;
      this.tags.data = tags;
      
      this.userManagementService.updateUser(this.user).subscribe(x => {
      });
    });

  }
  deleteTag(id: any) {
    if(window.confirm('Do you want to go ahead?')) {
      this.userManagementService.deleteTag(id).subscribe((res) => {
        this.tempTag = this.tempTag.filter((tag: { _key: any; }) => tag._key != id);
        this.tags.data = this.tempTag;
      })
    }
  }
  openEditTagDialog(tagKey: any) {
    const modalRef = this.modalService.open(EditTagComponent, {
      size: 'lg'
    });
    modalRef.componentInstance.data = { tagKey: tagKey};

    modalRef.result.then(c => {
      var tags = this.user.tags || [];
      var tagsId = this.user.tagsId || [];
      
      var uptag = tags.filter(tag => tag._key != c._key);
      uptag.push(c)

      this.user.tagsId = tagsId;
      this.tags.data = uptag;

      this.userManagementService.updateUser(this.user).subscribe(x => {
      });
    });

  }

  // isEnabled(tagKey: string): boolean {
  //   if (this.user && Array.isArray(this.user.tags)) {
  //     return !!this.user.tags.filter(x => !!x).find(x => x._key === tagKey);
  //   } else {
  //     return false;
  //   }
  // }

  handleSubIdList(userId: any, tagKey: any): void {
    this.router.navigateByUrl('/user-management/edit/' + userId + '/subids/' + tagKey);
  }
  
}
